import moment from 'moment';
import React, { FC, useEffect } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Col, Row } from 'reactstrap';
import { InputGroupInline, InputType, WindowComponent, WindowComponentProps } from 'summer';
import { hasEmpresa } from '../../../../../dependencies/empresaDependency';
import { hasEmpresaMFP } from '../../../../../dependencies/empresaMFPDependency';
import { hasGrupoEmpresaConfig } from '../../../../../dependencies/grupoEmpresaConfigDependency';
import { getDefaultReportBasicValues } from '../../../../../models/dtos/ger/reportBasicValues';
import { getDefaultReportViewOptions, ReportViewOptions } from '../../../../../models/dtos/ger/reportViewOptions';
import { ATIVOS, PERFIL_VINCULO_ENUM_VALUES } from '../../../../../models/enumerated/mfp/perfilVinculoEnum';
import { Reducers } from '../../../../../store/ducks';
import { GlobalState } from '../../../../../store/ducks/global.duck';
import ReportForm from '../../../../components/report/ReportForm';
import { AniversarianteRV, CLASS_KEY, REPORT_EVENT, REPORT_TITLE } from './aniversarianteRV';

const DEFAULT_VALUES: AniversarianteRV = {
  ...getDefaultReportBasicValues(REPORT_TITLE),
  gerarCabecalhoPadraoAdministradora: true,
  perfil: ATIVOS.value,
};

const DEFAULT_OPTIONS: ReportViewOptions = {
  ...getDefaultReportViewOptions(REPORT_EVENT),
  exibirConfiguracaoDadosEmpresa: true,
};

const AniversarianteReportView: FC<WindowComponentProps & RouteComponentProps> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const { handleSubmit, ...formProps } = useForm<AniversarianteRV>({ defaultValues: DEFAULT_VALUES, mode: 'onBlur' });

  useEffect(
    () => {
      if (globalReducer.globalParameter.empresaMFP) {
        const {
          empresaMFP: { referencia },
        } = globalReducer.globalParameter;
        setTimeout(() => {
          formProps.setValue('referencia', referencia);
          referenciaChangeHandler();
        }, 800);
      }
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  const referenciaChangeHandler = () => {
    const mon = moment(formProps.getValues().referencia, 'YYYY-MM');
    formProps.setValue('title', `${REPORT_TITLE} ${mon.format('MMMM')}`);
  };

  return (
    <WindowComponent
      title="Relação de Aniversariantes"
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[
        () => hasEmpresa(globalReducer.globalParameter),
        () => hasEmpresaMFP(globalReducer.globalParameter),
        () => hasGrupoEmpresaConfig(globalReducer.globalParameter),
      ]}
    >
      <ReportForm
        {...props}
        handleSubmit={handleSubmit}
        formProps={formProps}
        localStorageKey={CLASS_KEY}
        routeProps={props}
        reportBasicValues={DEFAULT_VALUES}
        reportViewOptions={DEFAULT_OPTIONS}
      >
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true } }}
              id="referencia"
              isPrependLabel={true}
              label="Informe a Referência"
              type={InputType.REFERENCE}
              inputProps={{ autoFocus: true }}
              onChange={referenciaChangeHandler}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="perfil"
              label="Situação"
              isPrependLabel={true}
              placeholder="Informe a Situação"
              formProps={{ ...formProps, validation: { required: true } }}
              inputDropDownProps={{
                optionsProvider: PERFIL_VINCULO_ENUM_VALUES,
                selectProps: { isClearable: false },
              }}
              type={InputType.DROP_DOWN}
            />
          </Col>
        </Row>
      </ReportForm>
    </WindowComponent>
  );
};

export default AniversarianteReportView;
